button.btn1 {
  cursor: pointer;
  border-radius: 20px;
  border: 1px solid #1563d8;
  border: 1px solid var(--main-color);
  background: #1563d8;
  background: var(--main-color);
  color: #fff;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 45px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-out;
}

button:hover {
  background: #2193b0;
  background: var(--secondary-color);
}

button:active {
  transform: scale(0.95);
}

button:focus {
  outline: none;
}

button.ghost1 {
  cursor: pointer;
  border-radius: 20px;
  border: 1px solid #1563d8;
  border: 1px solid var(--main-color);
  background: #1563d8;
  background: var(--main-color);
  color: #fff;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 45px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-out;
  background-color: transparent;
  border-color: #fff;
}

button.ghost1:hover {
  background: #fff;
  color: #2193b0;
  color: var(--secondary-color);
}

.form1 {
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  text-align: center;
}

input {
  border-radius: 10px;
  background-color: #eee;
  border: none;
  padding: 12px 15px;
  margin: 8px 0;
  width: 100%;
  font-family: inherit;
}

.container1 {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
  overflow: hidden;
  height: 768px;
  width: 480px;
  max-width: 100%;
}

.form-container {
  position: absolute;
  top: 0;
  width: 100%;
  transition: all 0.6s ease-in-out;
}

.sign-in-container {
  top: 0;
  height: 50%;
  z-index: 2;
}

.container1.right-panel-active .sign-in-container {
  transform: translateY(100%);
}

.sign-up-container {
  top: 0;
  height: 50%;
  opacity: 0;
  z-index: 1;
}

.container1.right-panel-active .sign-up-container {
  transform: translateY(100%);
  opacity: 1;
  z-index: 5;
  animation: show 0.6s;
}

@keyframes show {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }
  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}

.overlay-container {
  position: absolute;
  left: 0;
  top: 50%;
  height: 50%;
  width: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}

.container1.right-panel-active .overlay-container {
  transform: translateY(-100%);
}

.overlay {
  background: #2193b0;
  background: var(--secondary-color);
  background: linear-gradient(
    135deg,
    #1563d8,
    #2193b0);
  background: var(--gradient);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #fff;
  position: relative;
  top: -100%;
  width: 100%;
  height: 200%;
  transform: translateY(0);
  transition: transform 0.6s ease-in-out;
}

.container1.right-panel-active .overlay {
  transform: translateY(50%);
}

.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  left: 0;
  width: 100%;
  height: 50%;
  transform: translateY(0);
  transition: transform 0.6s ease-in-out;
}

.overlay-left {
  transform: translateY(-20%);
}

.container1.right-panel-active .overlay-left {
  transform: translateY(0);
}

.overlay-right {
  bottom: 0;
  transform: translateY(0);
}

.container1.right-panel-active .overlay-right {
  transform: translateY(20%);
}

@media (min-width: 768px) {
  body {
    margin: -20px 0 50px;
  }

  .container1 {
    width: 850px;
    max-width: 100%;
    height: 550px;
  }

  .form-container {
    top: 0;
    height: 100%;
    width: 50%;
  }

  .sign-in-container {
    left: 0;
    width: 50%;
    height: 100%;
  }

  .container1.right-panel-active .sign-in-container {
    transform: translateX(100%);
  }

  .sign-up-container {
    left: 0;
    width: 50%;
    height: 100%;
  }

  .container1.right-panel-active .sign-up-container {
    transform: translateX(100%);
  }

  .overlay-container {
    left: 50%;
    top: 0;
    height: 100%;
    width: 50%;
  }

  .container1.right-panel-active .overlay-container {
    transform: translateX(-100%);
  }

  .overlay {
    top: 0;
    left: -100%;
    height: 100%;
    width: 200%;
    transform: translateX(0);
  }

  .container1.right-panel-active .overlay {
    transform: translateX(50%);
  }

  .overlay-panel {
    top: 0;
    height: 100%;
    width: 50%;
    transform: translateX(0);
  }

  .overlay-left {
    transform: translateX(-20%);
  }

  .container1.right-panel-active .overlay-left {
    transform: translateX(0);
  }

  .overlay-right {
    right: 0;
    top: 0;
    left: 50%;
    transform: translateX(0);
  }

  .container1.right-panel-active .overlay-right {
    transform: translateX(20%);
  }
}

.NavbarItems {
    background: linear-gradient(90deg,rgb(110,94,254) 0%,rgba(73,63,252,1) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* font-size: 1.2rem; */
    padding-top: 1rem;
}

.navbar-logo{
    color: #fff;
    justify-content: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;

}



.fa-react{
    margin-left: 0.5rem;
    font-size: 1.6rem;
}


.nav-menu{
    display: grid;
    grid-template-columns: repeat(5,auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin: 0 2rem 1rem 0rem;
    padding-top: 10px;
}


.nav-links {
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav-links:hover{
    background-color: #fff;
    border-radius: 4px;
    text-decoration: none;
    transition: all 0.2s ease-out;

}


.fa-bars {
    color: #fff;
}
.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
    text-decoration: none;
}

@media screen and (max-width:960px){
    .NavbarItems{
        position: relative;
    }

    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 610px;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }


.nav-menu.active{
    background-color: #6668f4;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
}

.nav-links{

    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
}

.nav-links:hover{
    background-color: #7577fe;
    border-radius: 0;

}

.navbar-logo{
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%,50%);
}


.menu-icon{
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%,60%);
    font-size: 1.8rem;
    cursor: pointer;
}

.fa-times{
    color: #fff;
    font-size: 2rem;
}

.nav-links-mobile{
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: #4ad9e4;
    text-decoration:none;
    color: #fff;
    font-size: 1.5rem;
}
.nav-links-mobile:hover{
    background: #fff;
    color: #6d76f7;
    transition: 250ms;
}

}























:root {
    --primary : #3acbf7;

}

.btn{
    padding:8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn:hover{
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    color: #6568f4;
    transition: 250ms;

}


.btn--primary{
    background-color: #3acbf7;
    background-color: var(--primary);
}

.btn--outline{
    background-color: transparent;
    color: #fff;
    padding: 8 20px;
    border-radius: 4px;
    border: 1px solid #3acbf7;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium{
    padding: 8 20px;
    border-radius: 4px;
    color: #fff;
    font-size: 18px;
}



















